body {
  padding: 0.05em;
}

section[data-section-wrapper] {
  padding: 1em;
}

[data-name="name"] {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

[data-name="name"] > H3 {
  white-space: nowrap;
}

div[data-entry-value] {
  display: flex;
  flex-direction: row;
  margin-bottom: 0.25em;
  margin-left: 0.5em;
  margin-top: 0.5em;
}

[data-entry] {
  padding: 0.2em;
  padding-top: 0.5em;
  padding-left: 0.5em;
}

[data-entry-description] {
  font-weight: 600;
  display: flex;
}

[data-section-wrapper] {
  border: thin solid black;
  margin: 1em;
}

[data-name="section"] {
  background-color: lightgray;
}

[data-section-name] {
  flex-grow: 2;
}

label {
  flex-direction: row-reverse;
  display: flex;
  vertical-align: bottom;
  padding: 0.25em;
}

input:checked ~ span {
  font-weight: 600;
  color: black;
  text-decoration: underline;
}

input ~ span {
  margin: 0.2em;
  cursor: pointer;
}

input[type="radio"] {
  display: none;
}

[data-value="-1"] {
  background-color: #dd8888;
}

[data-value="1"] {
  background-color: #e69b00;
}

[data-value="2"] {
  background-color: #e5de00;
}

[data-value="3"] {
  background-color: #96d294;
}

div[data-header] {
  display: flex;
  flex-direction: column;
}

[data-survey-title] {
  text-align: center;
}

[data-actions]::before {
  content: "v";
}

[data-input-value] {
  background-color: #6693f5;
  color: white;
  font-weight: 600;
}

[data-entry-value]:first-child {
  border-radius: 50px 0px 0px 20px;
  color: yellow;
}

[data-total-score] {
  position: fixed;
  bottom: 55px;
  left: calc(100% - 220px);
  background-color: white;
  border: thin solid darkgray;
  padding: 0.5em 0.25em 0.5em 0.25em;
  border-radius: 20px;
  box-shadow: 7px 5px 5px #555555;
}

[data-team-name],
[data-assessment-date],
[data-assessment-id],
[data-assessment-display-administrator] {
  padding: 0.8em;
  margin-left: 5em;
  margin-right: 5em;
  margin-bottom: 0.2em;
  text-align: center;
  font-size: 18pt;
  font-weight: 600;
  color: #444444;
  border-radius: 10px;
  border: thin gray solid;
}

[data-hide-administrator="true"] {
  display: none;
}

[data-menu] {
  position: absolute;
  top: 5px;
  left: calc(100% - 155px);
  font-size: 17pt;
  display: flex;
  flex-direction: column;
  justify-content: right;
  white-space: nowrap;
  width: 150px;

  cursor: pointer;
}

.flex-row-container {
  display: flex;
}

.pad-top {
  padding-top: 1em;
}

@keyframes show {
  from {
    opacity: 0%;
    display: none;
  }

  to {
    opacity: 100%;
    display: block;
  }
}

@keyframes show-reverse {
  from {
    opacity: 100%;
    display: none;
  }

  to {
    opacity: 0%;
    display: block;
  }
}

@keyframes animate {
  0% {
    width: 0px;
    height: 0px;
  }

  30% {
    width: 50px;
    height: 0px;
  }

  60% {
    width: 50px;
    height: 80px;
  }
}

[data-menu-options] {
  display: flex;
  flex-direction: column;
}

[data-menu-options] {
  cursor: default;
}

input:not(:checked) ~ [data-menu-options] {
  animation-name: show;
  animation-duration: 4s;
  animation-direction: reverse;
  display: none;
}

input:checked ~ [data-menu-options] {
  border: thin solid gray;
  padding: 0.2em;
  animation-name: show;
  animation-duration: 1s;
  display: flex;
  background-color: white;
}

[data-toggle-menu-checkbox] {
  display: none;
}

[data-menu-options] > button {
  margin: 0.25em;
  border-radius: 5px;
  cursor: pointer;
}

[data-menu-hamburger] {
  cursor: pointer;
}

[data-hide-score-display="true"] {
  visibility: hidden;
}

[data-title] {
  text-align: center;
}

[data-survey-create-instance-name] {
  text-align: center;
}

[data-url-send-container] {
  display: flex;
  flex-direction: column;
}

[data-url-direction] {
  text-align: center;
}

[data-url-instance] {
  color: red;
  font-weight: 600;
  word-break: break-all;
}
input[type="button"].question-distributer,
[data-copy-url],
[start-post-process-button] {
  padding: 0.5em 3em 0.5em 3em;
  font-size: 1.3em;
  color: white;
  font-weight: 600;
  background-color: #6693f5;
  border-radius: 10px;
  margin: 0 auto;
  display: block;
  cursor: pointer;
}

input[type="number"].question-distributer {
  padding: 0.5em;
  font-size: larger;
  text-align: center;
  display: block;
  margin: 0 auto;
  margin-bottom: 0.25em;
  padding-left: 1.1em;
  border-radius: 0.3em;
  border: thin lightgrey solid;
}

[data-assessment-id-container] {
  display: flex;
  flex-direction: column;
}

[data-assessment-id-container-text] {
  font-size: 1.5em;
  display: block;
  text-align: center;
}

[data-complete-assessment-container] {
  display: flex;
  flex-direction: row;
  position: fixed;
  bottom: 0px;
  background-color: white;
  border-top: thin solid #555555;
  width: 100%;
  padding-bottom: 5px;
  padding: 15px;
}

[data-complete-assessment-container-next-steps] {
  display: flex;
  flex-direction: column;
  background-color: white;
  border-top: thin solid #555555;
  width: 100%;
  padding-bottom: 5px;
  padding: 15px;
}

[data-prepare-for-processing] {
  display: block;
  text-align: center;
}

[data-url-to-send-completed] {
  margin-bottom: 1em;
  padding: 1em 0em 1em 0em;
  word-break: break-all;
  display: flex;
  flex-direction: row;
}

[data-complete-url-container] {
  text-align: center;
  margin-bottom: 2em;
}
[data-container] {
  display: flex;
  flex-direction: row;
  margin-bottom: 0.5em;
}
[data-spacer] {
  flex-basis: 30%;
}

[data-admin] {
  font-weight: 600;
}

[data-complete-assessment] {
  flex-grow: 1;
  text-align: center;
}

[data-prepare-to-send-link],
[data-return-to-survey-link],
button.default {
  font-weight: 600;
  text-decoration: none;
  padding: 8px;
  border-radius: 10px;
  color: white;
  border: 1px solid black;
  padding: 0.5em 3em 0.5em 3em;
  font-size: 1.3em;
  color: white;
  font-weight: 600;
  background-color: #6693f5;
  border-radius: 10px;
}

[data-survey-container] {
  margin-bottom: 60px;
}
[data-went-in-error-container] {
  text-align: center;
}

button {
  border: 1px solid black;
}

.align-center {
  text-align: center;
}
